import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/entrenador',
    redirect: '/entrenador/inicio',
    component: () => import('../views/entrenadorView.vue'),
    children: [
      {
        path: 'inicio',
        name: 'inicioEntrenador',
        component: () => import('../components/agenda.vue')
      },
      {
        path: 'entrenamientos', //listado
        name: 'entrenamientosEntrenador',
        component: () => import('../components/planesEntrenamiento.vue')
      },
      {
        path: 'entrenamientos/:idPlanSemanal', //semana
        name: 'rutinaEntrenador',
        component: () => import('../components/planesDiarios.vue')
      },
      {
        path: 'entrenamientos/:idPlanSemanal/:dia', //diario
        name: 'diaEntrenador',
        component: () => import('../components/planView.vue')
      },
      {
        path: 'clientes', //listado
        name: 'clientesEntrenador',
        component: () => import('../components/clientesList.vue')
      },
      {
        path: 'clientes/:id',
        name: 'clienteEntrenador',
        component: () => import('../components/infoClienteView.vue')
      },
      {
        path: 'perfil',
        name: 'perfilEntrenador',
        component: () => import('../views/perfilView.vue')
      },
    ]
  },
  {
    path: '/cliente',
    redirect: '/cliente/inicio',
    component: () => import('../views/clienteView.vue'),
    children:[
      {
        path: 'inicio',
        name: 'iniciocliente',
        component: () => import('../components/agenda.vue')
      },
      {
        path: `entrenamientos/:idPlanSemanal`, //semana
        name: 'rutinacliente',
        component: () => import('../components/planesDiarios.vue')
      },
      {
        path: 'entrenamientos/:idPlanSemanal/:dia', //diario
        name: 'diacliente',
        component: () => import('../components/planView.vue')
      },
      {
        path: 'perfil',
        name: 'perfilcliente',
        component: () => import('../views/perfilView.vue')
      }
    ]
  },
  {
    path: '/login',
    alias: '/',
    name: 'login',
    component: () => import('../views/loginView.vue')
  }
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router

