import store from '../store/index.js'

    function encontrarIndexPlanSemanal(id){
        return store.state.planList.findIndex(plan => plan.idPlanSemanal === id)
    }

    function encontrarIndexPlanDiario(idSemanal, idDiario){
        const indexSemanal = this.encontrarIndexPlanSemanal(idSemanal)
        return store.state.planList[indexSemanal].planDiario.findIndex(plan => plan.dia === idDiario)
    }

    function encontrarPlanSemanal(id){
        return store.state.planList.find( plan => plan.idPlanSemanal == id )
    }

    function encontrarPlanDiario(idSemanal, dia){
        const planSemanal = this.encontrarPlanSemanal(idSemanal)
        return planSemanal.planDiario.find( plan => plan.dia == dia )
    }

    function encontrarIndexEjercicio(indexSemanal, indexDiario, ejercicioId){
        return store.state.planList[indexSemanal].planDiario[indexDiario].ejerciciosList.findIndex(ejercicio => ejercicio.id === ejercicioId)
    }

    function encontrarCliente(id){
        return store.state.clientesList.find( cliente => cliente.id == id)
    }

    function encontrarNombrePlan(id){
        const plan = this.encontrarPlanSemanal(id)
        return plan.nombrePlanSemanal
    }

    function convertirNumeroADias(diaNum){
        let dia
            switch(diaNum){
                case '1': dia='Lunes'
                break;
                case '2': dia='Martes'
                break;
                case '3': dia='Miercoles'
                break;
                case '4': dia='Jueves'
                break;
                case '5': dia='Viernes'
                break;
                case '6': dia='Sabado'
                break;
                case '7': dia='Domingo'
                break;
            }
            return dia
    }

    function convertirDiaANumero(dia){
        let diaNum
            switch(dia){
                case 'Lunes': diaNum='1'
                break;
                case 'Martes': diaNum='2'
                break;
                case 'Miercoles': diaNum='3'
                break;
                case 'Jueves': diaNum='4'
                break;
                case 'Viernes': diaNum='5'
                break;
                case 'Sabado': diaNum='6'
                break;
                case 'Domingo': diaNum='7'
                break;
            }
            return diaNum
    }

    function rutinaCliente(){
        return store.state.planList[0].idPlanSemanal
    }
export default { rutinaCliente, encontrarNombrePlan, encontrarCliente, encontrarIndexEjercicio, encontrarPlanDiario, encontrarPlanSemanal, encontrarIndexPlanSemanal, encontrarIndexPlanDiario, convertirNumeroADias, convertirDiaANumero }